.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
}
.navlinks {
  display: flex;
  list-style: none;
  padding-left: 0;
}
.navlinkitem {
  padding-right: 2rem;
  margin: auto;

}
.navlinkitem :hover {
  color: white;
  background-color: green;

}
